/* src/styles.css */

/* Set global font and background */
body {
  font-family: "Roboto", sans-serif;
}

/* Header styles */
.header {
  background-color: #374151;
  color: white;
}

/* Button styles */
.btn {
  display: inline-block;
  padding: 10px 20px;
  background: #20002c; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #cbb4d4,
    #20002c
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #cbb4d4,
    #20002c
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #357ac9;
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  font-weight: bold;
}

.form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #cbd5e0;
  border-radius: 5px;
}

/* src/components/Homepage.css */

/* Base styling for the homepage */
.max-w-6xl {
  max-width: 1200px;
  margin: 0 auto;
}

.p-8 {
  padding: 2rem;
}

.text-2xl {
  font-size: 1.5rem;
  font-weight: 600;
}

.font-semibold {
  font-weight: 600;
}

.mb-4 {
  margin-bottom: 1rem;
}

/* Styling for the book grid */
.grid {
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.grid-cols-2 {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.grid-cols-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.grid-cols-4 {
  grid-template-columns: repeat(4, minmax(0, 1fr));
}

.gap-4 {
  gap: 1rem;
}

/* Limit image size */
.book-card img {
  max-width: 100%;
  height: auto;
}

