body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

/* Set font and background color for the entire app */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
}

/* Apply some global styling to elements */
a {
  text-decoration: none;
  color: #333; /* Change link color to match your design */
